body {
  background-color: #282c34;
}

.App {
  display:flex;
  flex-direction: column;
  text-align: center;
  max-width: 100%;
  margin: 0 auto;
  height:100vh;
}

.App header {
  background-color: #181717;
  height: 15vh;
  min-height: 50px;
  width: 100%;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 99;
  padding: 10px;
  box-sizing: border-box;
}

.App section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 100vh;
  background-color: rgb(40, 37, 53);
}

main {
  padding: 10px;
  height: 80vh;
  margin: 10vh 0 10vh;
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
}

main::-webkit-scrollbar {
  width: 0.25rem;
}

main::-webkit-scrollbar-track {
  background: #1e1e24;
}

main::-webkit-scrollbar-thumb {
  background: #6649b8;
}

form {
  height: 10vh;
  position: fixed;
  bottom: 0;
  background-color: rgb(24, 23, 23);
  width: 100%;
  display: flex;
  font-size: 1.5rem;
}

form button {
  width: 20%;
  background-color: rgb(56, 56, 143);
}


input {
  display: flex;
  line-height: 1.5;
  width: 100%;
  font-size: 1.5rem;
  background: rgb(58, 58, 58);
  color: white;
  outline: none;
  border: none;
  padding: 0 10px;
}

button {
  background-color: #282c34; /* Green */
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  cursor: pointer;
  font-size: 1.25rem;
}

button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}


.sign-in {
  display: flex;
  color: #282c34;
  background: white;
  max-width: 400px;
  margin: 0 auto;
  align-items: center;
  border-radius: 20px;
}

.sign-out{
  display: flex;
  color: white;
  margin: 0 auto;
  position: relative;
  left: 78vh;
  max-width: 100vh;
}


ul, li {
  text-align: left;
  list-style: none;
}

p {
  padding: 10px 20px;
  border-radius: 25px;
  position: relative;
  bottom: 0%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  text-align: center;
}

.message {
  display: flex;
  align-items: center;
}


.sent {
  flex-direction: row-reverse;
}

.sent p {
  color: white;
  background: #0b93f6;
  align-self: flex-end;
}
.received p {
  background: #e5e5ea;
  color: black;
}

img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin: 2px 5px;
}
.goog{
  width: 24px;
  height: 24px;
  align-items: center;
}
.mymy {
  display: flex;
  align-items: center;
  justify-content: center;
}